.list {
  font: {
    family: $global-font;
    size: rem(16px); }
  color: $secondary-color;
  letter-spacing: 0.4px;
  line-height: 32px;
  list-style: none; }

.list--sticky {
  position: fixed;
  top: 0; }

.list__heading {
  font: {
    family: $global-font-secondary;
    size: rem(14px);
    weight: $global-font-weight-bold; }
  line-height: rem(30px);
  color: $primary-color;
  text-transform: uppercase;
  letter-spacing: 0.4px;
  margin: rem($global-v-gutter 0 10px); }

.list__link {
  color: $secondary-color;
  text-decoration: none;
  display: block;
  padding: rem(0 10px);
  margin-left: rem(-10px);
  border-radius: 5px;

  &--active {
    background: $tertiary-color; }

  &--disabled {
    color: $secondary-lighten-color;
    pointer-events: none; } }

.list__dl {
  font-family: $global-font;
  font-weight: $global-font-weight; }

.list__dt {
  font-size: rem(14px);
  margin-bottom: rem(10px);
  color: $quaternary-color; }

.list__dd {
  font-size: rem($global-font-size);
  margin-left: 0;
  margin-bottom: rem(20px);
  line-height: 1.4; }

.list__ul,
.list__ol {
  margin: rem(0 0 0 30px);
  padding: 0;
  line-height: 1.8;
  font-weight: $global-font-weight; }

.list__ul {
  list-style: square; }



